              width: 1.04rem;
              height: 0.54rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join2.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(3) {
              width: 1.45rem;
              height: 0.47rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join3.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(4) {
              width: 1.51rem;
              height: 0.33rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join4_m.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(5) {
              width: 1.37rem;
              height: 0.30rem;
              margin: 0.32rem auto 0;
              background: url("../../assets/img/invest-join5_m.png") no-repeat center;
              background-size: 100%;
            }
            &:nth-child(6) {
              width: 1.13rem;
              height: 0.40rem;
              margin: 0.32rem auto 0.79rem;
              background: url("../../assets/img/invest-join6_m.png") no-repeat center;
              background-size: 100%;
            }
          }
        }
      }
      &-media {
        width: 100%;
        background-color: #ffffff;
        &-wrap {
          width: 100%;
          margin: 0;
          padding: 0.93rem 0.38rem 1.25rem 0.38rem;
          box-sizing: border-box;
          &-list {
            display: none;
          }
          &-swiper {
            display: block;
            margin-top: 0.35rem;
            .slide-wrap-img {
              width: 2.7rem;
              height: 1.5rem;
              margin: 0 auto;
              img {
                width: 100%;
                height: 1.5rem;
              }
            }
            .slide-wrap-title {
              opacity: 0.8;
              font-size: 16px;
              color: #000000;
              line-height: 0.22rem;
              margin-top: 0.4rem;
              white-space: nowrap;
              letter-spacing: 0;
            }
            .slide-wrap-time {
              opacity: 0.4;
              font-size: 14px;
              color: #000000;
              letter-spacing: 0;
              line-height: 0.2rem;
              margin-top: 0.12rem;
              text-align: center;
            }
            .slide-wrap-detail {
              opacity: 0.6;
              font-size: 14px;
              color: #000000;
              line-height: 0.26rem;
              margin-top: 0.3rem;
            }
            .slide-wrap-more {
              display: block;
              font-size: 14px;
              margin-top: 0.15rem;
              line-height: 0.2rem;
              text-align: center;
              color: #FF8903;
            }
          }
        }
      }
    }
  }
</style>
<script>
  import vHeader from '@/components/header/index'
  import vFooter from '@/components/footer/index'
  import $ from 'jquery'
  export default {
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          autoplay: {
            disableOnInteraction: false,
            delay: 3000
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          }
        },
        mobielSwiperOption: {
          autoplay: {
            disableOnInteraction: false,
            delay: 3000
          }
        }
      }
    },
    components: {
      vHeader,
      vFooter
    },
    methods: {
      //  
      goSscroll(selector, height) {
        $('html, body').animate({
          scrollTop: $(selector).offset().top - height
        }, 1000)
      },
      handleSeePdfClick() {
        window.open('/#/pdf')
      },
      handleOpenClick() {
        window.open('http://new.xinhuanet.com.lv/a/2018/0506/3167.html')
      }
    }
  }
</script>



